// images references in the manifest
import "../../assets/icon-16.png";
import "../../assets/icon-32.png";
import "../../assets/icon-80.png";

/* global document, Office */

var forwardingData;
var changeKey; //retrieved in getItemDataCallback, used for forwarding and deleting the email

Office.onReady(info => {
  if (info.host === Office.HostType.Outlook) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("run").onclick = run;
  }
  
  var item = Office.context.mailbox.item;
  var fwdto = Office.context.mailbox.item.from.emailAddress
  var submitter = Office.context.mailbox.item.sender.emailAddress
  var subject = item.subject
  
  // Populate TaskPane Fields
  document.getElementById("item-to").innerHTML = "" + submitter
  document.getElementById("item-from").innerHTML = "<br>" + fwdto  + "<br>Ashley.Racich@flydenver.com<br>Rachel.Marion@flydenver.com"
  document.getElementById("item-subject").innerHTML = "" + subject 
  
});


export async function run() {

// Get a reference to the current message
var item = Office.context.mailbox.item;
var from = Office.context.mailbox.item.from.emailAddress
var to = Office.context.mailbox.item.sender.emailAddress
var subject = "[CORA] FW: " + item.subject
var prepend = "By this email I am forwarding your email to our CORA Representative for response."

// recipients seperated by commas
var mailto = "benjamin.britton@flydenver.com"

   
  const message = {
    type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
    message: "This email was forwarded for CORA.",
    icon: "Icon.80x80",
    persistent: true
  };

  // Show a notification message
  Office.context.mailbox.item.notificationMessages.replaceAsync("action", message);
  
  Office.initialize = function (reason) {
        $(document).ready(function () {
            app.initialize();

        });
    };
  
      try {

        //Get a handle to the 'item' object for the active email 
        var item = Office.cast.item.toItemRead(Office.context.mailbox.item);
          
        //Get values from the page controls
        forwardingData = new ForwardingData(mailto, subject, prepend, item.itemId);

        //Call EWS to get the source item; then we need to make another EWS request to forward it
        var mailbox = Office.context.mailbox;
        mailbox.makeEwsRequestAsync(getItemDataRequest(item.itemId), getItemDataCallback);
    } catch (e) {
       document.getElementById("item-console").innerHTML = 'Error! ' + status + '[in beginForwardEmail()]:' + e
    }
  
 
  // Be sure to indicate when the add-in command function is complete
  event.completed();
}

function getXMLDocParser(response)
{
    var xmlDoc;
    if (window.DOMParser) {
        var parser = new DOMParser();
        xmlDoc = parser.parseFromString(response, "text/xml");
    }
    else // Older Versions of Internet Explorer
    {
        xmlDoc = new ActiveXObject("Microsoft.XMLDOM");
        xmlDoc.async = false;
        xmlDoc.loadXML(response);
    }
    return xmlDoc;
}


function ForwardingData(to, subject, body, sourceid) {
    this.to = to;
    this.subject = subject;    
    this.body = body;    
    this.sourceid = sourceid;
};

function getItemDataRequest(item_id) {
    var request;

    request = '<?xml version="1.0" encoding="utf-8"?>' +
        '<soap:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance"' +
        '               xmlns:m="http://schemas.microsoft.com/exchange/services/2006/messages"' +
        '               xmlns:xsd="http://www.w3.org/2001/XMLSchema"' +
        '               xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/"' +
        '               xmlns:t="http://schemas.microsoft.com/exchange/services/2006/types">' +
        '  <soap:Header>' +
        '    <RequestServerVersion Version="Exchange2013" xmlns="http://schemas.microsoft.com/exchange/services/2006/types" soap:mustUnderstand="0" />' +
        '  </soap:Header>' +
        '  <soap:Body>' +
        '    <GetItem' +
        '                xmlns="http://schemas.microsoft.com/exchange/services/2006/messages"' +
        '                xmlns:t="http://schemas.microsoft.com/exchange/services/2006/types">' +
        '      <ItemShape>' +
        '        <t:BaseShape>IdOnly</t:BaseShape>' +
        '      </ItemShape>' +
        '      <ItemIds>' +
        '        <t:ItemId Id="' + item_id + '"/>' +
        '      </ItemIds>' +
        '    </GetItem>' +
        '  </soap:Body>' +
        '</soap:Envelope>';

    return request;
}

function getItemDataCallback(asyncResult) {
	
	document.getElementById("item-console").innerHTML = "GetItemCallback";
	
    if (asyncResult == null) {
       document.getElementById("item-console").innerHTML = 'Error134', '[in getItemDataCallback]: null result';
        return;
    }

    if (asyncResult.error != null) {
        document.getElementById("item-console").innerHTML = 'Error139', '[in getItemDataCallback]: ' + asyncResult.error.message;
    }
    else {

        var errorMsg;
        var prop = null;
        try {
            var response = $.parseXML(asyncResult.value);
            var responseDOM = $(response);

            if (responseDOM) {
                prop = responseDOM.filterNode("t:ItemId")[0];
            }

        } catch (e) {
            errorMsg = e;
        }
		
		if (!prop) {
            if (errorMsg) {
                document.getElementById("item-console").innerHTML ='Error! [in getItemDataCallback]: Failed to retrieve item data (' + errorMsg + ')';
            }
            else { document.getElementById("item-console").innerHTML = 'Error! [in getItemDataCallback]: Failed to retrieve item data'; }

            return;
        }


        changeKey = prop.getAttribute("ChangeKey");
        // Now that we have a ChangeKey value, we can use EWS to forward the mail item.

        var addresses;
        var addressesSoap = '';
        
        addresses = forwardingData.to.split(",");
        
        // The following loop will build an XML fragment that we will insert into the SOAP message
        for (var address = 0; address < addresses.length; address++) {
            //Need to trim the addresses, as a leading space will cause an error!
            addressesSoap += "<t:Mailbox><t:EmailAddress>" + addresses[address].trim() + "</t:EmailAddress></t:Mailbox>";
        }
		
        //Now, forward the item

        document.getElementById("item-console").innerHTML = "Addresses Assigned";

        var mailbox = Office.context.mailbox;       
        mailbox.makeEwsRequestAsync(forwardItemRequest(forwardingData.subject, forwardingData.sourceid, addressesSoap, changeKey, forwardingData.body), forwardItemCallback);
		document.getElementById("item-console").innerHTML = 'Submitting Forward.'
    }
}

function forwardItemCallback(asyncResult) {

    if (asyncResult == null) {
        app.showNotification('Error!', '[in forwardItemCallback]: null result');
        return;
    }

    if (asyncResult.error != null) {
        app.showNotification('Error!', '[in forwardItemCallback]: ' + asyncResult.error.message);
    }
    else {

        var errorMsg;
        var prop = null;
        try {
            var response = $.parseXML(asyncResult.value);
            var responseDOM = $(response);

            if (responseDOM) {                
                prop = responseDOM.filterNode("m:ResponseCode")[0];
            }

        } catch (e) {
            errorMsg = e;
        }
        if (!prop) {
            if (errorMsg) {
                document.getElementById("item-console").innerHTML = 'Error! [in forwardItemCallback]: ' + errorMsg;
            }
            else { document.getElementById("item-console").innerHTML = 'Error! [in forwardItemCallback]: Failed to parse response'; }

            return;
        } else {
            //Verify forward result
            if (prop.textContent == "NoError") {
                document.getElementById("item-console").innerHTML = "The email has been forwarded.";                
            }
            else {
                document.getElementById("item-console").innerHTML = 'Error! [in forwardItemCallback]:' + prop.textContent;
            }
        }
    }
}

function forwardItemRequest(subject, item_id, recipients, changeKey, body) {
    var request;

    // The following string is a valid SOAP envelope and request for forwarding
    // a mail item. Note that we use the item_id value to specify the item we are interested in,
    // along with its ChangeKey value that we have just determined
    // We also provide the XML fragment to specify the recipient addresses
    
    request = '<?xml version="1.0" encoding="utf-8"?>' +
        '<soap:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance"' +
        '               xmlns:m="http://schemas.microsoft.com/exchange/services/2006/messages"' +
        '               xmlns:xsd="http://www.w3.org/2001/XMLSchema"' +
        '               xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/"' +
        '               xmlns:t="http://schemas.microsoft.com/exchange/services/2006/types">' +
        '  <soap:Header>' +
        '    <RequestServerVersion Version="Exchange2013" xmlns="http://schemas.microsoft.com/exchange/services/2006/types" soap:mustUnderstand="0" />' +
        '  </soap:Header>' +
        '  <soap:Body>' +
        '    <m:CreateItem MessageDisposition="SendAndSaveCopy">' +
        '      <m:Items>' +
        '        <t:ForwardItem>' +
        '         <t:Subject>' + subject + '</t:Subject>' +
        '          <t:ToRecipients>' + recipients + '</t:ToRecipients>' +
        '          <t:ReferenceItemId Id="' + item_id + '" ChangeKey="' + changeKey + '" />' +
        '          <t:NewBodyContent BodyType="HTML">' + body + '</t:NewBodyContent>' +
        '        </t:ForwardItem>' +
        '      </m:Items>' +
        '    </m:CreateItem>' +
        '  </soap:Body>' +
        '</soap:Envelope>';

    return request;
}

// This function plug in filters nodes for the one that matches the given name.
// This sidesteps the issues in jquerys selector logic.
(function ($) {
    $.fn.filterNode = function (node) {
        return this.find("*").filter(function () {
            return this.nodeName === node;
        });
    };
})(jQuery);